function StackItem({ name }) {
  const iconMap = {
    "React.js": faReact,
    Styled: faPaintBrush,
    Recharts: faChartArea,
    Axios: faProjectDiagram,
    "Node.js": faNodeJs,
    Keras: faBrain,
    'TF Keras': faBrain,
    'Serverless': faAws,
    'Pipelines': faBitbucket,
    'Docker': faDocker,
    Firebase: faDatabase,
    Supabase: faDatabase,
    Java: faJava,
    "TCP/IP": faNetworkWired,
    Threads: faNetworkWired,
    Python: faPython,
    Pandas: faChartArea,
    SciPy: faFlask,
    "React Native": faReact,
    GraphQL: faDatabase,
    JavaScript: faJs,
    Unity: faUnity,
    ".NET C#": faWindows,
  }
  const icon = iconMap[name]
  return (
    <div key={name} style={{ margin: "min(1vw,5px)" }}>
      <CentredDiv>
        <FontAwesomeIcon icon={icon} size="lg" />
      </CentredDiv>
      {name}
    </div>
  )
}

function Stack({ stack }) {
  return (
    <StackContainer>
      {stack.map(name => (
        <StackItem name={name} key={name} />
      ))}
    </StackContainer>
  )
}

function FeatureIcon({ name }) {
  const FeatureIcons = {
    powers: faSuperpowers,
    beer: faBeer,
    bitcoin: faBitcoin,
    bike: faBiking,
    gaming: faGamepad,
    chart: faChartBar,
  }
  const FeatureIcon = FeatureIcons[name]
  return <FontAwesomeIcon icon={FeatureIcon} size="lg" />
}

function Links({ links }) {
  const icons = {
    "Source Code": () => (
      <FontAwesomeIcon
        icon={faCircle}
        color="black"
        size="lg"
        style={{ marginRight: 5 }}
      />
    ),
    Demo: () => (
      <FontAwesomeIcon
        icon={faEye}
        color="black"
        size="lg"
        style={{ marginRight: 5 }}
      />
    ),
    Notebook: () => (
      <FontAwesomeIcon
        icon={faBookOpen}
        color="tomato"
        size="lg"
        style={{ marginRight: 5 }}
      />
    ),
    Paper: () => (
      <FontAwesomeIcon
        icon={faGoogleDrive}
        size="lg"
        color="#00abbf"
        style={{ marginRight: 5 }}
      />
    ),
    "App Store": () => (
      <FontAwesomeIcon
        icon={faAppStoreIos}
        size="lg"
        color="#00abff"
        style={{ marginRight: 5 }}
      />
    ),
    "Play Store": () => (
      <FontAwesomeIcon
        icon={faGooglePlay}
        size="lg"
        color="#00abbf"
        style={{ marginRight: 5 }}
      />
    ),
    YouTube: () => (
      <FontAwesomeIcon
        icon={faYoutube}
        size="lg"
        color="red"
        style={{ marginRight: 5 }}
      />
    ),
  }

  return (
    <SourceContainer>
      {links.map(({ link, type }) => {
        const Icon = icons[type]
        return (
          <a target="_blank" rel="noopener noreferrer" href={link} key={link}>
            <Icon />
            {type}
          </a>
        )
      })}
    </SourceContainer>
  )
}

function BasicCard(props) {
  const { stack, links, feature, title, description } = props

  return (
    <div className="basicCard">
      <div style={styles.cardHeader}>
        <Stack stack={stack} />
        <FeatureIcon name={feature} />
      </div>
      <div style={{ padding: 20 }}>
        <h2 style={{ fontSize: 25 }}>{title}</h2>
        <div>{description}</div>
      </div>
      <Links links={links} />
    </div>
  )
}

